@import url(https://fonts.googleapis.com/css?family=Cinzel:400,700,900);
.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

/* FOOTER */
footer {
  display: flex;
  justify-content: space-between;
  font-size: 1em;
  padding: 0.5em 1em;
  border-top: 1px solid #152e47;
  /*box-shadow: 0 -1px 1px rgba(0,0,0,0.3);*/
  background: linear-gradient(#234E6F, #152e47)
}

@media screen and (max-width: 480px) {
  footer {
    font-size: 0.8em;
  }
}

main {
  flex: 1 1;
  margin: 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
/*BOARD*/
.board {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-flow: row wrap;
}

/*GRID*/
.row {
  display: flex;
}

.grid {
  border: 5px solid #234E6F;
  border-radius: 5px;
}

.cell {
  display: flex;
  outline: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  align-items: center;
  justify-content: center;
  height: 45px;
  width: 45px;
  background: #cad1d6;
  border: 2px solid #234E6F;
  box-sizing: border-box;
}

/*LABELS*/
.labElement {
  height: 45px;
  width: 45px;
  font-size: 2.3em;
  font-weight: bold;
  text-align: center;
}

.labelH {
  display: flex;
  margin-left: 40px;
  justify-content: center;
  flex-basis: 100%;
  margin-bottom: -10px;
}

.labelV {
  margin-top: 5px;
  margin-right: -10px;
}

.labelV .labElement {
  display: flex;
  align-items: center;
}

/*DESCRIPTION*/
.description {
  margin-top: 1em;
  font-size: 1.5em;
  text-align: center;
}

.description p {
  margin: 0.5em;
}

/*WIN*/
.win {
  padding: 1.5em 3.5em;
  position: absolute;
  background: rgba(12, 28, 44, 0.9);
  margin: 5em auto;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #0a1927;
  z-index: 11;
}

.win p {
  font-size: 2.2em;
  margin: 20px;;
}

/*BUTTON*/
.btn {
  font-size: 1.7em;
  margin: 20px;
  margin-top: 10px;
  padding: 1em;
  border-radius: 5px;
  border: 1px solid #152e47;
  box-shadow: 0 1px 1px rgba(0,0,0,0.3);
  background: linear-gradient(#234E6F, #152e47);
  cursor: pointer;
  letter-spacing: 0.1em;
  color: #f3d352;
  text-shadow: 1px 1px 0 #000;
  font-family: 'Cinzel', serif;
  font-weight: bold;
  outline: none;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.5);
  z-index: 10;
}

.example-appear {
  opacity: 0.01;
}

.example-appear.example-appear-active {
  opacity: 1;
  transition: opacity 5s ease-in;
}

/*MOBILE*/
@media screen and (max-width: 480px) {
  .cell {
    height: 35px;
    width: 35px;
  }
  .cell img {
    width: 25px;
    height: 25px;
  }
  .labElement {
    height: 35px;
    width: 35px;
    font-size: 2em;
  }

  .labelH {
    margin-left: 30px;
  }

  .description {
    margin-top: 1.5em;
    font-size: 1.3em;
  }

  .win {
    padding: 0.5em 2em;
    font-size: 90%;
    left: 30px;
    right:30px;
  }
}

/* HEADER */
header {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

h1 {
  font-size: 4.4em;
  font-weight: bold;
  margin: 0.3em;
  text-align: center;
}

nav {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-basis: 460px;
  margin: 0;
  padding: 0;
}

li.nav {
  width: 90px;
  text-align: center;
  border-radius: 5px;
  font-size: 1.5em;
  font-weight: bold;
  padding: 1em 0.5em;
  border: 1px solid #152e47;
  box-shadow: 0 1px 1px rgba(0,0,0,0.3);
  background: linear-gradient(#234E6F, #152e47);
  cursor: pointer;
}

li.nav:focus, img:focus {
  outline: none;
}

/*CHECK*/
.check {
  font-size: 1.6em;
  position: absolute;
  background: rgba(12, 28, 44, 0.9);
  padding: 1em;
  margin: 10px auto;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #0a1927;
  width: 300px;
}

/*RULES*/
.rules {
  font-size: 1.3em;
  line-height: 1.5em;
  position: absolute;
  background: rgba(12, 28, 44, 0.9);
  padding: 1em;
  margin: 10px auto;
  border-radius: 5px;
  max-width: 500px;
  border: 1px solid #0a1927;
}

.rules p {
  text-align: center;
}

.rules img {
  float: left;
}

.rules img:first-child {
  float: right;
}

.rules ul {
  margin: 1em 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.rules li {
  margin: 0 1em 0 2em;
  padding: 0.2em;
  list-style-type: circle;
}

/*MOBILE*/
@media screen and (max-width: 480px) {
  h1 {
    font-size: 2.8em;
  }

  .check {
    left: 10px;
    right:10px;
  }

  .rules {
    left: 10px;
    right:10px;
  }
}

@media screen and (max-width: 400px) {
  h1 {
    font-size: 2.6em;
  }

  nav ul {
    margin: 0 1em;
  }

  li.nav {
    width: 72px;
    font-size: 1em;
    padding: 1em 0.5em;
  }

  .rules {
  font-size: 1em;
  }
}
html {
  font-size: 16px;
}

body {
  padding: 0;
  font-size: 62.5%;
  letter-spacing: 0.1em;
  color: #f3d352;
  text-shadow: 1px 1px 0 #000;
  font-family: 'Cinzel', serif;
  background-image: url(/static/media/background.df39bd8b.jpg);
}

